import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { InputMoney } from 'components/common';
import { Row, Card, Form, Input } from 'antd';
import {
  getSubsidizeObjectById,
  updateSubsidizeObject,
  createSubsidizeObject,
} from 'services/subsidizeObject';
import { getQueryParams } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
    supportAmt: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormSubsidiesObjectPage' })
class FormSubsidiesObjectPage extends Component {
  state = {
    subsidiesId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ subsidiesId: queryParamId });
      this.getDataById(queryParamId);
    }
  };

  getDataById = async id => {
    const res = await getSubsidizeObjectById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { subsidiesId } = this.state;
    try {
      if (!subsidiesId) {
        await createSubsidizeObject(values);
      } else {
        await updateSubsidizeObject({ id: subsidiesId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { subsidiesId } = this.state;
    return (
      <Card title={subsidiesId ? 'Cp nht i tng tr cp' : 'Thm i tng tr cp'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="K hiu" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input disabled={subsidiesId} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tn i tng" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="S tin tr cp" hasFeedback>
              {getFieldDecorator('supportAmt', {
                initialValue: initialValue.supportAmt,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(<InputMoney style={{ width: 200 }} min="0" max="9999999999" />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSubsidiesObjectPage {...props} />
  </Page>
));



